import { Router } from '@angular/router';
import { UserService } from './../../core/services/user.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit {
  public loginForm: FormGroup;
  constructor(private userService: UserService, public snackBar: MatSnackBar, private router: Router) { }

  ngOnInit() {
    this.loginForm = new FormGroup ( {
      email: new FormControl(null, Validators.required),
      password: new FormControl(null, Validators.required)
    });

  }

  login() {
    this.userService.login(this.loginForm.value).subscribe(
      (response: any) => {
        console.log(response);
          if (response.status) {
            this.userService.setLoginStatus(response.token);
            this.router.navigateByUrl('/');
          }else {
            const snackbarRef = this.snackBar.open('nvalid Username/Password', 'Try Again', {
              duration: 5000,
          });
      }
      });
  }

}



