import { serverUrl } from '../../config/url';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';



@Injectable()
export class ProductService {

  constructor(private http: HttpClient) { }

  getProductsofCategory(category): any {
    return  this.http.get(serverUrl + 'protected/getproducts/' + category);
   }


   addProduct(category, product): any {
     return  this.http.post(serverUrl + 'protected/addproduct/' + category , product);
   }

   getAllData() {
     return this.http.get(serverUrl + 'protected/data');
   }

}



