import { CategoryService } from './../../core/services/category.service';
import { Response } from '@angular/http';
import { UploadService } from './../../core/services/upload.service';
import { Component, OnInit , ElementRef, ViewChild } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  public uploadForm: FormGroup;
  public userImage: File;
  public data;
  @ViewChild('fileInput') user_image;

  constructor(private uploadService: UploadService, private categoryService: CategoryService) {}

  ngOnInit() {
    this.uploadForm = new FormGroup ({
      'file' : new FormControl ()
    });
  }

  onSubmit() {
    const uploadFile = this.user_image.nativeElement;
    if ( uploadFile.files && uploadFile.files[0]) {
      this.userImage = uploadFile.files[0];
    }
    const finalUploadFile: File = this.userImage;
    const formData: FormData = new FormData();
    formData.append('uploadedFile', finalUploadFile , finalUploadFile.name);
    this.uploadService.upload(formData).subscribe(
      (response: Response) => {
        this.data = response;
        this.data.forEach(element => {
          if (element.status === 1) {
              element.status = true;
          } else {
            element.status = false;
          }
        });
      }
    );
  }




  addCategories() {
    this.data.forEach(cat => {
      delete cat['sno'];
    });
      this.uploadService.addCategory(this.data)
      .subscribe(
        (response: Response) => {
          console.log(response);
        }
      );
    }
  }



