import { serverUrl } from '../../config/url';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {JwtHelper } from 'angular2-jwt';
@Injectable()
export class UserService {
  jwtHelper: JwtHelper = new JwtHelper();
  constructor(private http: HttpClient, private router: Router, private matbar: MatSnackBar) {
  }


  registerUser(user) {
    return this.http.post(serverUrl + 'register', user);
  }

  login(user) {
    return this.http.post(serverUrl + 'login', user);
  }

  setLoginStatus(token) {
    sessionStorage.setItem('token', token);
    this.matbar.open('You have been Logged In !', 'Home', {
      duration: 5000
    });
  }

  checkLoginStatus () {
    const token = sessionStorage.getItem('token');
    if (token) {
          return !this.jwtHelper.isTokenExpired(token);
    }else {
      return false;
    }
}

  logout() {
    sessionStorage.clear();
    this.router.navigateByUrl('/');
    this.matbar.open('You have been Logged Out !', 'OK', {
      duration: 5000
    });
  }

  getToken () {
    return sessionStorage.getItem('token');
  }

}



