import { serverUrl } from '../../config/url';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class ReportService {

  constructor(private http: HttpClient) { }


  getDashboardData(data) {
   return this.http.post(serverUrl + 'protected/getdashboarddata', data);
  }

  getSalesSummary(data) {
    return this.http.post(serverUrl + 'protected/getsalessummary', data);
  }

  getSalesByData(data, viewBy) {
    return this.http.post(serverUrl + 'protected/getsalesby/' + viewBy, data);
  }

  getHourlySalesData(data) {
    return this.http.post(serverUrl + 'protected/gethourly', data);
  }

  getSalesFeed(data) {
    return this.http.post(serverUrl + 'protected/getsalesfeed', data);
  }
}



