import { ReportService } from '../../../core/services/report.service';
import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

@Component({
  selector: 'sales-by',
  templateUrl: './sales-by.component.html',
  styleUrls: ['./sales-by.component.css'],
})
export class SalesByComponent implements OnInit {
  public spinnerStatus = false;
  public searchResult;
  public p = 1;
  public tableView = 'category';
  public view = 'category';
  constructor(private reportService: ReportService) { }

  ngOnInit() {

  }

  getData(data) {
    this.spinnerStatus = true;
    this.reportService.getSalesByData(data, this.view).subscribe(
      (response: any) => {
      this.searchResult = response;
      this.spinnerStatus = false;
      this.tableView = this.view;
      }
    );
  }

  getViewBy(data) {
    this.view = data;
  }

}



