import { Component, OnInit } from '@angular/core';
import { CategoryService } from './../../core/services/category.service';
import { ProductService } from './../../core/services/product.service';
import { FormGroup , FormControl , Validators, FormBuilder } from '@angular/forms';
import {MatSnackBar} from '@angular/material';
import { Response } from '@angular/http';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  public allCategories = [];
  public viewCategory;
  public selectedCategory;
  public allData;
  public product = [];
  public productForm: FormGroup;

  constructor(private categoryService: CategoryService, private productService: ProductService, public snackBar: MatSnackBar) { }

  ngOnInit() {
       this.productService.getAllData().subscribe(
         (response) => {
           this.allData = response;
        }
       );

       this.categoryService.getCategories().subscribe(
        (res) => {
          this.allCategories = res[0]['category'];
         });

    this.productForm = new FormGroup( {
      'name': new FormControl(null, Validators.required),
      'price': new FormControl (null, Validators.required)

    });

     this.allCategories = this.categoryService.getCategories().subscribe(
       (response: Response) => {
        this.allCategories = response[0]['category'];

       }
     );
  }

  addProduct() {
    this.productService.addProduct(this.selectedCategory, this.productForm.value).subscribe(
      (response: Response) => {
        this.allData = response;
        this.allCategories = response[0]['category'];
        this.getProductsOfCategory();
        this.snackBar.open('Product Added Successfully', 'Ok', {
          duration: 5000,
        });
      }
    );
  }

  getProductsOfCategory() {
    this.product = [];
    this.allData.forEach(element => {
      if (element.categoryName === this.viewCategory) {
        this.product = element.products;
      }
    });
  }

}



