import { ReportService } from '../../../core/services/report.service';
import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

@Component({
  selector: 'hourly-sales',
  templateUrl: './hourly-sales.component.html',
  styleUrls: ['./hourly-sales.component.css'],
})
export class HourlySalesComponent implements OnInit {
  public spinnerStatus = false;
  public searchResult;
  public p = 1;
  constructor(private reportService: ReportService) { }

  ngOnInit() {

  }

  getData(data) {
    this.spinnerStatus = true;
    this.reportService.getHourlySalesData(data).subscribe(
      (response: any) => {
      this.searchResult = response;
      console.log(response);
      this.spinnerStatus = false;
      }
    );
  }


}



