import { Response } from '@angular/http';
import { Component, OnInit } from '@angular/core';
import { CategoryService } from './../../core/services/category.service';
import {MatSnackBar} from '@angular/material';
import { FormGroup , FormControl, Validators } from '@angular/forms';
@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {
  public allCategories = [];
  public categoryForm: FormGroup;
  constructor(private categoryService: CategoryService, public snackBar: MatSnackBar) { }

  ngOnInit() {
     this.allCategories = this.categoryService.getCategories().subscribe(
      (res) => {
        this.allCategories = res[0]['category'];
       },
       (err) => {
         console.log(err)
         console.log('Something went wrong!');
       }
     );


    this.categoryForm = new FormGroup({
      name: new FormControl(null, Validators.required),
      description: new FormControl(null, Validators.required),
      status : new FormControl (null, Validators.required)
    });

  }

  addCategory() {
    this.categoryService.addCategories(this.categoryForm.value).subscribe(
      (response: Response) => {
        this.allCategories = response[0]['category'];
        this.snackBar.open('Category Added Successfully', 'Ok', {
          duration: 5000,
        });
      }
    );
  }


}



