import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter  } from '@angular/core';


@Component({
  selector: 'date-picker',
  styleUrls: ['date.component.css'],
  templateUrl: 'date.component.html'
})
export class DateComponent implements OnInit {
  public currentDate = new Date();
  public dateForm: FormGroup;
  @Output() dateRange: EventEmitter<any> = new EventEmitter<any>();
  @Output() viewBy: EventEmitter<any> = new EventEmitter<any>();
  @Input() salesBy = false;
  @Input() salesByData = ['category', 'product'];

  ngOnInit() {
    this.dateForm = new FormGroup({
      startDate: new FormControl(null, Validators.required),
      endDate: new FormControl(null, Validators.required),
      salesBy: new FormControl('category', Validators.required)
    });
  }

  sendData() {
    const startDate: Date = this.dateForm.get('startDate').value;
    const endDate: Date = this.dateForm.get('endDate').value;
    endDate.setHours(23);
    endDate.setMinutes(59);
    endDate.setSeconds(59);
    this.dateRange.emit({startDate: startDate.toISOString(), endDate: endDate.toISOString()});
    this.viewBy.emit(this.dateForm.get('salesBy').value);
  }

  changeCategory() {
    this.viewBy.emit(this.dateForm.get('salesBy').value);
  }


}




